body {
  text-align: center;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

h1 {
  color: #fff;
  background-color: #0078d7;
  padding: 10px;
}

.container {
  max-width: 500px;
  margin: 20px auto;
}

input[type="text"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 80%;
  margin: 5px 0;
  padding: 10px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #0078d7;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
}

button:hover {
  background-color: #005bb5;
}

ul {
  padding: 0;
  list-style: none;
}

li {
  text-align: left;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin: 10px 0;
  padding: 10px;
}

.delete-btn {
  color: #fff;
  cursor: pointer;
  float: right;
  background-color: red;
  border: none;
  padding: 5px;
}

#chatbot-container {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 320px;
  padding: 10px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

#chat-history {
  border-bottom: 1px solid #ccc;
  height: 220px;
  margin-bottom: 10px;
  padding: 5px;
  font-size: 14px;
  overflow-y: auto;
}

#chat-input {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: calc(100% - 70px);
  padding: 8px;
  font-size: 14px;
}

#send-btn {
  cursor: pointer;
  color: #fff;
  background-color: #0078d7;
  border: none;
  border-radius: 4px;
  padding: 8px 12px;
  font-size: 14px;
  transition: background-color .3s;
}

#send-btn:hover {
  background-color: #005bb5;
}

.history {
  background-color: #f1f1f1;
  border-radius: 4px;
  margin-bottom: 12px;
  padding: 5px;
  font-size: 13px;
}

@media (width <= 768px) {
  .container, .filters {
    width: 90%;
  }

  #chatbot-container {
    width: 280px;
    bottom: 10px;
    right: 10px;
  }
}
